.taskActivity-container {
    .activity-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .comment-container {
        .taskDetails-coment {
            display: flex;
            // margin-top: 10px;
            .avatar {
                margin-inline-start: 30px;
            }
            .text-area-comment {
                resize: none;
                padding: 5px 10px;
                width: 100%;
                height: 50px;
                border: none;
                background: #fff !important;
                font-family: Poppings, "Segoe UI", sans-serif;
                background-color: rgba(9, 30, 66, 0.04);
                box-shadow: none;
                border: 0;
                border-radius: 2px;
                display: block;
                min-height: 20px;
                text-decoration: none;
                box-shadow: inset 0 0 0 2px #dfe1e6;
                margin-inline-start: 10px;
                &:focus {
                    border-radius: 2px;
                    outline: none;
                }
                &:hover {
                    background: #fff;
                }
            }
        }
        .primary-btn {
            margin-inline-start: 70px;
            margin-block-start: 10px;
        }

        .activity {
            display: flex;
            align-items: center;
            margin-block-start: 10px;
        }
    }

    .activity {
        display: flex;
        align-items: center;
        margin-top: 10px;
        margin-inline-start: 30px;
    }
    .activity-description {
        font-size: 14px;
        margin-left: 10px;
        p {
            &.comment-txt {
                color: #172b4d;
                background-color: #fff;
                border-radius: 2px;
                box-shadow: 0 1px 2px -1px #091e4240, 0 0 0 1px #091e4214;
                display: inline-block;
                margin: 4px 2px 0 0;
                overflow: hidden;
                text-overflow: ellipsis;
                max-width: 100%;
                padding: 5px 10px;
            }
        }
        span {
            font-weight: bold;
        }
        .date{
            font-size: 12px;
        }
      
    }
}
