.TaskDetails-modal {
    .main-screen {
        width: 100vw;
        height: 100vh;
        position: fixed;
        z-index: 900;
        top: 0;
        background-color: rgba(0, 0, 0, 0.64);
        overflow-y: auto;
    }

    .taskDetails-container {
        position: absolute;
        background-color: $modalBgc;
        border-radius: 2px;
        left: 50%;
        -webkit-transform: translate(-50%);
        width: 700px;
        min-height: 500px;
        padding: 1.25rem;
        z-index: 2;
        font-size: 0.875rem;
        color: $font;
        z-index: 999;
    

        .taskDetails-body {
            display: flex;
            // flex-direction: column;
            min-height: 24px;
            .task-details {
                flex-basis: 80%;
            }
            .task-details > * {
                margin-bottom: 10px;
            }

            .task-actions{
                padding-inline-start: 20px;
            }
       
        }


        // .task-actions{
        //     display: flex;
        //     flex-wrap: wrap;
        //     justify-content:space-between;
        // }

        .taskDetails-task-status {
            display: flex;
            flex-wrap: wrap;
            .task-members-preview,
            .taskDetails-labels,
            .taskDetails-DueDate{
                margin-inline-start: 30px;
                .date-dueDate{
                    display: flex;
                    align-items: center;
                    p{
                        margin-inline-start: 5px;
                    }
                    #myCheck{
                        height: 30px;
                    }
                }
                .complete{
                    font-size: 12px;
                    color: #172b4d;
                    display: flex;
                    align-items: center;
                    border-radius: 3px;
                    padding: 3px;
                    background-color: #61BD4F;
                }
            
            }
        }
        .taskDetails-img{
            .uploaded-img{
                max-height: 250px;
                max-width: 482px;
                object-fit: cover;
                margin-inline-start: 30px;
            }
            .secondary-btn{
                margin-inline-start: 30px;
                margin-block-start: 10px;
            }
        }

        .section-title {
            display: flex;
            align-items: center;
            font-size: 18px;
            margin-bottom: 10px;
            .icon {
                font-size: 26px;
                margin-inline-end: 5px;
            }
        }
    }
}
